import Vue from 'vue';
import contains from '../../../common/scripts/contains';
export default Vue.extend({
    components: {
        XPicker: () => import('../../../common/views/components/emoji-picker.vue').then(m => m.default)
    },
    props: {
        x: {
            type: Number,
            required: true
        },
        y: {
            type: Number,
            required: true
        }
    },
    mounted() {
        this.$nextTick(() => {
            const width = this.$el.offsetWidth;
            const height = this.$el.offsetHeight;
            let x = this.x;
            let y = this.y;
            if (x + width - window.pageXOffset > window.innerWidth) {
                x = window.innerWidth - width + window.pageXOffset;
            }
            if (y + height - window.pageYOffset > window.innerHeight) {
                y = window.innerHeight - height + window.pageYOffset;
            }
            if (this.$root.isMobile) {
                this.$el.style.top = y + 50 + 'px';
            }
            else {
                this.$el.style.left = x + 'px';
                this.$el.style.top = y + 'px';
            }
            for (const el of Array.from(document.querySelectorAll('body *'))) {
                el.addEventListener('mousedown', this.onMousedown);
            }
        });
    },
    methods: {
        onMousedown(e) {
            if (!contains(this.$el, e.target) && (this.$el != e.target)) {
                e.preventDefault();
                this.close();
                return false;
            }
            else {
                return true;
            }
        },
        chosen(emoji) {
            this.$emit('chosen', emoji);
            this.close();
        },
        close() {
            for (const el of Array.from(document.querySelectorAll('body *'))) {
                el.removeEventListener('mousedown', this.onMousedown);
            }
            this.$emit('closed');
            this.destroyDom();
        }
    }
});
